<style lang=less>
    @import '../../../assets/index.less';
    @import './gooleVerify.less';
</style>
<template>
    <div class="base-info goole-verify">
         <p class="title">
          <span></span>{{$t('center.gooleVerify[0]')}}
        </p>
        <div class="top-tips">
          {{$t('center.gooleVerify[1]')}}
        </div>
        <!-- 步骤1 -->
        <!-- 公用样式box -->
        <div class="box">
          <p class="box-title">
            <span class="circle">1</span>{{$t('center.gooleVerify[2]')}}
          </p>
          <div class="box-tips">
            {{$t('center.gooleVerify[3]')}}
          </div>
          <div class="box-tips">
            {{$t('center.gooleVerify[4]')}}
          </div>
          <!-- 定制样式 -->
          <div class="img-box">
            <img class="app-store" src="../../../assets/img/center/app_store.png"/>
            <img src="../../../assets/img/center/goole_play.png"/>
          </div>
        </div>

        <!-- 步骤2 -->
        <!-- 公用样式box -->
        <div class="box">
          <p class="box-title">
            <span class="circle">2</span>{{$t('center.gooleVerify[5]')}}
          </p>
          <div class="box-tips">
            {{$t('center.gooleVerify[6]')}}
          </div>
          <!-- 定制样式 -->
          <div>
            <!-- <img class="code-img" :src="codeImgUrl"> -->
            <img class="code-img" :src="qrCodeurl">
            <span class="code">{{gooleCode}}</span>
            <em class="copy" @click="copyCode">{{$t('center.gooleVerify[7]')}}</em>
          </div>
        </div>

        <!-- 步骤3 -->
        <!-- 公用样式box -->
        <div class="box">
          <p class="box-title">
            <span class="circle">2</span>{{$t('center.gooleVerify[8]')}}
          </p>
          <div class="box-tips">
            {{$t('center.gooleVerify[9]')}}
          </div>
          <!-- 定制样式 -->
          <div style="width: 467px;padding-bottom: 25px;">
            <el-input v-model="verifyCode"></el-input>
          </div>
        </div>

        <!-- 步骤4 -->
        <!-- 公用样式box -->
        <div class="box" style="border:none;">
          <p class="box-title">
            <span class="circle">2</span>{{$t('center.gooleVerify[11]')}}{{isTelLogin ? '手机' : '邮箱'}}{{$t('center.gooleVerify[10]')}}
          </p>
          <div class="box-tips">
            {{$t('center.gooleVerify[10]')}}
          </div>
          <!-- 定制样式 -->
          <div style="width: 467px;padding-bottom: 25px;position: relative;">
            <el-input v-model="telCode"></el-input>
            <span v-loading="getCodeLoading" element-loading-spinner="el-icon-loading" class="get-code" @click="getCodeRequest">{{telCodeText}}</span>
          </div>
        </div>
        <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.gooleVerify[12]')}}</el-button>
    </div>
</template>
<script>
import {sendMsg,sendEmailMsg} from '@/api/public.js'
import {getCooleParams,bindGoole} from "@/api/user.js"
export default {
  data () {
    return {
      submitLoading:false,
      codeImgUrl:'../../../assets/img/center/heigher-identity1.png',
      gooleCode:'',
      verifyCode:'',
      telCode:'',
      telCodeText: this.$t('center.gooleVerify[13]'),//验证码处显示的内容
      countDownNumber:60,//倒计时秒数
      countDownTimer:null,//定时器
      ableGetCode:true,//验证码按钮能否点击
      getCodeLoading:false,
      qrCodeurl:'',
      sessionData:{},
      isTelLogin: this.$public.checkMobile(sessionStorage.getItem('mobile'))
    }
  },
  created () {
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
    getCooleParams().then(res => {
      if(res.data.status == 200){
        this.qrCodeurl = res.data.data.local_url
        this.gooleCode = res.data.data.command
      } else{
        this.$public.msg(res.data.msg,'error',this)
      }
    })
  },
  watch: {
  },
  methods: {
    countDown(){//验证码倒计时
      this.ableGetCode = false
      let countDownCopy = this.countDownNumber
      this.countDownTimer = setInterval(()=>{
        if(this.countDownTimer && countDownCopy<=0){
          clearInterval(this.countDownTimer)
          this.telCodeText = this.$t('center.gooleVerify[14]')
          this.ableGetCode = true
          return 
        }
        this.telCodeText = countDownCopy
        countDownCopy--;
      },1000)
    },
    getCodeRequest(){//发送获取验证码请求接口
      if(!this.ableGetCode){return}
      this.ableGetCode = false
      this.getCodeLoading = true
      if(this.isTelLogin){//手机登录下发送短信验证码
        let _data = {
          mobile: sessionStorage.getItem('mobile'),
          mcode: this.sessionData.mcode,
          type:7
        }
        sendMsg(_data).then((res)=>{
          this.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.gooleVerify[15]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.ableGetCode = true
          }
        },
        ()=>{
          this.getCodeLoading = false
          this.$public.msg(this.$t('center.gooleVerify[16]'),'error',this)
          this.ableGetCode = true
        })
      } else{//发送邮箱验证码
        let _data = {
            email: sessionStorage.getItem('mobile')
        }
        sendEmailMsg(_data).then((res)=>{
          this.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.gooleVerify[15]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.ableGetCode = true
          }
        },
        ()=>{
          this.getCodeLoading = false
          this.$public.msg(this.$t('center.gooleVerify[16]'),'error',this)
          this.ableGetCode = true
        })
      }
      
    },
    copyCode(){
      this.copyText(this.gooleCode)
    },
    copyText(text, callback){
      let tag = document.createElement('input');
      tag.setAttribute('id', 'cp_hgz_input');
      tag.value = text;
      document.getElementsByTagName('body')[0].appendChild(tag);
      document.getElementById('cp_hgz_input').select();
      document.execCommand('copy');
      document.getElementById('cp_hgz_input').remove();
      if(callback) {callback(text)}
    },
    submit(){
      if(!this.$public.code(this.verifyCode)){
        this.$public.msg(this.$t('center.gooleVerify[17]'),'warning',this)
        return
      }
      if(!this.$public.code(this.telCode)){
        if(this.isTelLogin){
          this.$public.msg(this.$t('center.gooleVerify[18]'),'warning',this)
        } else{
          this.$public.msg(this.$t('center.gooleVerify[19]'),'warning',this)
        }
        return
      }
      this.submitLoading = true
      let _data = {
        dyGoodleCommand: this.verifyCode,
        code: this.telCode,
        account: sessionStorage.getItem('mobile')//手机号或邮箱
      }
      bindGoole(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.gooleVerify[20]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
        }
      })
    }
  }
}
</script>



